<template>
  <div class="index">
    <bread-nav :nav="nav"></bread-nav>
    <main-part>
      <div class="box detail-box">
        <table class="table mt15">
          <thead>
          <tr>
            <td class="title">姓名</td>
            <td class="content">{{data.name}}</td>
            <td class="title">年级</td>
            <td class="content">{{data.gradeList.map(e=> e.name).join('，')}}</td>
          </tr></thead>
          <tbody>
          <tr>
            <td class="title">科目</td>
            <td class="content">{{data.courseName}}</td>
            <td class="title">职务</td>
            <td class="content">{{data.position || '--'}}</td>
          </tr>
          <tr>
            <td class="title">职称</td>
            <td class="content">{{data.title}}</td>
            <td class="title">评定时间</td>
            <td class="content">{{data.acquireTime | substrTime}}</td>
          </tr>
          <tr>
            <td class="title">第一学历</td>
            <td class="content">{{$enums.Education.getName(data.education)}}</td>
            <td class="title">来校时间</td>
            <td class="content">{{data.joinSchoolTime |substrTime}}</td>
          </tr>
          <tr>
            <td class="title">身份证号码</td>
            <td colspan="3" class="content">{{data.idCard}}</td>
          </tr></tbody>
        </table>
        <p class="salary-title">{{$route.query.date}}工资条</p>

        <div class="salary-sheet">
          <ul>
            <li class="item" :key="key" v-for="(item,key) in salaryItem">
              <p class="title">{{item.label}}</p>
              <p class="number">{{item.value}}</p>
            </li>
          </ul>
        </div>
        <div class="btns">
          <div @click="$router.go(-1)" class="btn-clear">返回</div>
        </div>
      </div>
    </main-part>
  </div>

</template>
<script>
  export default {
    data() {
      return {
        nav: [{name: "工资信息", path: "go(-1)"}, {name: "工资详情", path: ""}],
        data: '',
        salaryItem: [
          //  '其中：浮动10%', '其中：校龄', '其中：工作年限奖', '应发数'
          {label: '基本工资',value: 'salaryBase'},
          {label: '其中：基本工资1',value: 'salaryBase1'},
          {label: '其中：之前浮动4%',value: 'salaryFloat4'},
          {label: '其中：浮动5%',value: 'salaryFloat5'},
          {label: '其中：浮动10%',value: 'salaryFloat10'},
          {label: '其中：校龄',value: 'schoolAge'},
          {label: '其中：工作年限奖',value: 'salaryWorkAge'}
        ]
      }
    },
    methods: {
      async getDetail() {
        let res = await this.$ajax({
          url: '/salary/getSalary.do',
          query: {
            id: this.$route.params.id
          }
        })
        this.salaryItem = [
          {label: '基本工资',value: res.salaryBase},
          {label: '应发工资',value: res.salaryShould},
          {label: '其中：基本工资1',value: res.salaryBase1},
          {label: '其中：之前浮动4%',value: res.salaryFloat4},
          {label: '其中：浮动5%',value: res.salaryFloat5},
          {label: '其中：浮动10%',value: res.salaryFloat10},
          {label: '其中：校龄',value: res.schoolAge},
          {label: '其中：工作年限奖',value: res.salaryWorkAge}
        ]
        res.otherSalaryList.forEach(e=>{
          this.salaryItem.push({
            label: e.salaryTemplateName,value: e.salaryValue
          })
        })
        this.salaryItem.push({
          label: '实发工资', value: res.salaryReality
        })
        this.data = res
      }
    },
    mounted() {
      this.getDetail()
    }
  }
</script>
